<template>
  <div class="sticky top-0 z-50 bg-gray-200 shadow-xl">
    <div class="">
      <nav
        class="
          container
          px-6
          py-2
          mx-auto
          flex flex-col justify-center items-center
        "
      >
        
        <div class="w-full flex items-center justify-between">
          
          <img src="@/assets/svg/clothes.svg" alt="" class="h-6 items-center hidden md:block">

          <span
            class="
              text-xl
              font-bold
              text-gray-800
              md:text-2xl
              hover:text-blue-400
              cursor-pointer
              font-serif
            "
            >Areen Style
          </span>

          <!-- Mobile menu button -->
            <div @click="showMenu = !showMenu" class="flex">
            <button
              type="button"
              class="
                text-gray-800
                hover:text-gray-400
                focus:outline-none focus:text-gray-400
              "
            >
              <svg viewBox="0 0 24 24" class="w-6 h-6 fill-current">
                <path
                  fill-rule="evenodd"
                  d="M4 5h16a1 1 0 0 1 0 2H4a1 1 0 1 1 0-2zm0 6h16a1 1 0 0 1 0 2H4a1 1 0 0 1 0-2zm0 6h16a1 1 0 0 1 0 2H4a1 1 0 0 1 0-2z"
                ></path>
              </svg>
            </button>
          </div>
        </div>


        <!-- Mobile Menu open: "block", Menu closed: "hidden" -->
        <ul
          :class="showMenu ? 'block' : 'hidden'"
          class="
            w-full
            mt-8
            space-y-4
            md:justify-center
            md:items-center
            md:space-x-10
            md:text-center
            md:mb-2
          "
        >
          <li class="block w-full">
            <a class="mx-4 px-4 py-3 block bg-gray-300 text-gray-900 text-xs font-semibold rounded hover:bg-gray-400" href="intent://send/+249117803213#Intent;scheme=smsto;package=com.whatsapp;action=android.intent.action.SENDTO;end">تواصل مع ارين استايل عبر الواتساب</a>
          </li>
          <li class="block w-full">
            <a class="mx-4 px-4 py-3 block bg-gray-300 text-gray-900 text-xs font-semibold rounded hover:bg-gray-400" href="intent://send/+249121174819#Intent;scheme=smsto;package=com.whatsapp;action=android.intent.action.SENDTO;end">تواصل مع المشرف عبر الواتساب</a>
          </li>
          <li class="block w-full">
            <a class="mx-4 px-4 py-3 block bg-gray-300 text-gray-900 text-xs font-semibold rounded hover:bg-gray-400" href="intent://send/+249921578765#Intent;scheme=smsto;package=com.whatsapp;action=android.intent.action.SENDTO;end">تواصل مع المصمم عبر الواتساب</a>
          </li>
        </ul>
      </nav>
    </div>




    <div id="tags" class="flex flex-row justify-center items-center flex-wrap ">
            <a href="#BestSeller" v-if="getBestSeller.length > 0" class="h-12 px-2 py-2 hover:bg-gray-300 text-center text-gray-700 border-bottom-width: 1px; border-gray-300 -px-1 whitespace-nowrap focus:outline-none">
                <span class="mx-1 text-xs md:text-base">
                    الأكثر مبيعا
                </span>
            </a>

            <a :href="'#' +NavBarTag.Category" v-for="NavBarTag,i in NavBarTags" :key="i" class="h-12 px-2 py-2 hover:bg-gray-300 text-center text-gray-700 border-bottom-width: 1px; border-gray-300 -px-1 whitespace-nowrap cursor-base focus:outline-none hover:border-gray-400">
                <span class="mx-1 text-xs md:text-base">
                    {{ NavBarTag.Category }}
                </span>
            </a>
        </div>
  </div>

</template>

<style>
  .tagActive{
    border-width: 1px;
    border-bottom-width: 0px;
    border-top-left-radius: 0.375rem/* 6px */;
    border-top-right-radius: 0.375rem/* 6px */;
  }
  .tagNotActive{
    border-bottom-width: 1px;
  }
</style>

<script>
import DataProducts from '@/Data/Products' 
export default {
  data() {
    return {
      showMenu: false,
      Products:DataProducts,
      NavBarTags:[]
    };
  },
  created(){
    this.getAllCategory() 
  },
  computed:{
      getBestSeller (){
          return this.Products.filter((item) => {
              if(item.bestSeller){
                  return item
              }
              return false
          })
        },
      },
  methods:{
    getAllCategory(){
      const seen = new Set();
      this.NavBarTags = this.Products.filter(el => {
        const duplicate = seen.has(el.Category);
        seen.add(el.Category);
        return !duplicate;
      });
    }
  }
};
</script>